.slider-container {
	display: flex;
	flex-wrap: nowrap;
	overflow-x: scroll;
	justify-content: center;
	align-items: center;
	margin: 0rem 2rem;
	padding: 3px;
	scroll-behavior: smooth;
}

.slider-wrapper {
	display: flex;
}

.btn {
	border: none;
	background-color: transparent;
	font-size: 1.5rem;
	padding: 0 10px;
	cursor: pointer;
}

.disable {
	opacity: 0.5;
	cursor: not-allowed;
}

.parent {
	width: 85%;
	overflow: hidden;
	margin: auto;
	padding: 1rem;
}
.slider-container {
	overflow-x: scroll;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0rem 2rem;
	padding: 3px;
 scroll-behavior: smooth;
}
.slider-wrapper {
	display: flex;
	align-items: center;
	width: 100%;
}
.btn {
	position: absolute;
	box-shadow: 0 0 6.1px 0.9px grey;
	padding: 5px;
	cursor: pointer;
	width: 20px;
	height: 20px;
	background: grey;
	text-align: center;
	color: white;
	border-radius: 50%;
}
.prev {
	left: 1rem;
}
.next {
	right: 1rem;
}
.disable {
	opacity: 0.5;
 	pointer-events: none;
}
.child {
	padding: 5px 20px;
	background: #59eb97;
	text-align: center;
	width: 100%;
	margin-right: 10px;
	border-radius: 21px;
}
